import _ from "lodash";
import React from "react";
import { CssBaseline, Grid, ThemeProvider, Typography, Tooltip } from "@material-ui/core";
import { Content } from "../Components/Content";
import { Feed } from "../Components/Feed";
import { Header } from "../Components/Header";
import { theme } from "./Theme";
import { Control } from "../Components/ControlContext";
import { graphql, useStaticQuery } from "gatsby";


const quotes = [
    {
        quote: "I never thought that words like \"product\" could ever leave my lips -- but something happened to me somewhere that made me lose my grip...",
        source: "Barenaked Ladies - Gordon - Box Set",
        link: "https://open.spotify.com/track/5DfP8PuTDKZGIFkjUc2Ztg?si=b5cLUw9DSEi-iW-GRyhENg",
    },
    {
        quote: "You say you wanna know everything about me?  Well here you go...",
        source: "Barenaked Ladies - Gordon - Box Set",
        link: "https://open.spotify.com/track/5DfP8PuTDKZGIFkjUc2Ztg?si=b5cLUw9DSEi-iW-GRyhENg",
    },
    {
        quote: "I clutch the wire fence until my fingers bleed...",
        source: "Rush - Grace Under Pressure - Red Sector A",
        link: "https://open.spotify.com/track/5YsrGiOhGUGzW89RXrLYGV?si=52W9u32fSc2A79zJEPhl1g",
    },
    {
        quote: "...The political illiterate is so stupid that he is proud and swells his chest saying that he hates politics...",
        source: "Bertolt Brecht",
        link: "https://www.goodreads.com/quotes/541442-the-worst-illiterate-is-the-political-illiterate-he-doesn-t-hear",
    },
    {
        quote: "All sunshine makes a desert.",
        source: "Arab proverb",
        link: null,
    },
];

interface Props extends React.ComponentProps<any>
{
    title?: string;
    subtitle?: string;
    showBack?: boolean;
}

// eslint-disable-next-line import/no-default-export
export default function WithFeed(props: Props)
{
    const songQuote = _.shuffle(quotes)[0];
    const data = useStaticQuery(query);

    return <>
        <ThemeProvider theme={theme}>

            <Control>

                <CssBaseline />

                <Grid container
                    justify="flex-end"
                    style={{
                        backgroundColor: "#dbdbdb",
                        marginBottom: "3vw",
                        paddingBottom: "1vw",
                    }}
                >
                    <Grid
                        item
                        container
                        xs={9}
                        direction="row"
                        spacing={0}
                        style={{
                            marginRight: "3vw",
                        }}
                    >
                        <Grid
                            item
                            container
                        >
                            <Header
                                title={ props.title }
                                subtitle={ props.subtitle }
                                showBack={ props.showBack }
                            />
                        </Grid>
                    </Grid>
                </Grid>

                <Grid container justify="flex-end">

                    <Grid
                        item
                        container
                        xs={9}
                        direction="row"
                        spacing={0}
                        style={{
                            marginBottom: "3vw",
                            marginRight: "3vw",
                        }}
                    >

                        <Grid
                            item
                            container
                        >

                            <Grid
                                item
                                container
                                direction="row-reverse"
                            >
                                <Grid item container xs={true}>
                                    <Content>
                                        { props.children }
                                    </Content>
                                </Grid>
                                <Grid item container xs={2}>
                                    <Feed />
                                </Grid>
                            </Grid>

                        </Grid>

                    </Grid>

                    <Grid
                        item
                        container
                        direction="row"
                        justify="flex-end"
                        style={{
                            marginBottom: "1em",
                            marginRight: "0.5em",
                        }}
                    >
                        <Typography
                            style={{
                                fontSize: "0.7em",
                                fontStyle: "italic",
                                cursor: "pointer",
                                marginRight: "auto",
                                marginLeft: "0.5em",
                            }}
                        >
                            { data.environment.version }
                        </Typography>

                        <Tooltip
                            title={songQuote.source}
                            onClick={songQuote.link ? () => window.open(songQuote.link, "_blank") : null}
                        >
                            <Typography
                                style={{
                                    fontSize: "0.7em",
                                    fontStyle: "italic",
                                    cursor: "pointer",
                                }}
                            >
                                { songQuote.quote }
                            </Typography>
                        </Tooltip>

                    </Grid>
                </Grid>
            </Control>
        </ThemeProvider>
    </>;
}

const query = graphql`
    query WithFeedQuery {
        environment {
            version
        }
    }
`;
